.container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: url('../../assets/photo/bg3.jpg');
    background-size: cover;
    background-position: center center;
    color: #fff;
    padding: 0
  }
  .nav{
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin:10px;
  }
  .nav span{
    margin-left: 20px;
  }
  .about-us{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-direction: column;
  }
  .who-we-are{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .who-we-are span{
    width: 60%;
    align-items: center;
    text-align: center;
  }
  .cards{
    display: flex;
    justify-content: center;
    align-items: center;
    margin:10px;
    flex-wrap: wrap;
  }
  .card-img{
    border-radius: 5px;
  }
  .cards .card{
    width: 16rem;
    margin: 20px;
  }
  .card p{
    font-size: 14px;
  }
  .social-media{
    width: 90%;
    display: flex;
    justify-content: flex-end;
  }
  .social-media i{
    margin:10px;
  }
  .active{
    border-bottom: 1px solid #fff;
  }
  .card-img1{
    width: 100%;
    height: 12rem;
    background-image: url('../../assets/photo/01.jpg');
    background-size: cover;
    background-position: center center;
  }
  .card-img2{
    width: 100%;
    height: 12rem;
    background-image: url('../../assets/photo/02.jpg');
    background-size: cover;
    background-position: center center;
  }
  .card-img3{
    width: 100%;
    height: 12rem;
    background-image: url('../../assets/photo/03.jpg');
    background-size: cover;
    background-position: center center;
  }
  .card-img4{
    width: 100%;
    height: 12rem;
    background-image: url('../../assets/photo/04.jpeg');
    background-size: cover;
    background-position: center center;
  }
  .logo{
    font-family: 'Dancing Script',cursive;
    font-size: 24px;
  }
 